import React, { Component, Fragment } from 'react'
import { withRouter } from 'react-router-dom'
import axios from 'axios'
import apiUrl from '../../apiConfig'
import Button from 'react-bootstrap/Button'

import ActivityForm from './ActivityForm'

class CreateActivity extends Component {
  state = {
    activity: {
      begin_date: '',
      end_date: '',
      activity_title: ''
    }
  }

  handleChange = event => {
    this.setState({
      activity: {
        ...this.state.activity,
        [event.target.name]: event.target.value
      }
    })
  }

  handleSubmit = event => {
    event.preventDefault()
    axios({
      method: 'POST',
      url: `${apiUrl}/activities`,
      headers: {
        'Authorization': `Bearer ${this.props.user.token}`
      },
      data: {
        activity: {
          'begin_date': this.state.activity.begin_date,
          'end_date': this.state.activity.end_date,
          'activity_title': this.state.activity.activity_title,
          'trip_id': parseInt(this.props.match.params.id),
          'user_id': this.props.user.id
        }
      }
    })
      .then(response => {
        this.props.alert({
          heading: 'Success!',
          message: 'You added an activity.',
          variant: 'success'
        })
        this.props.history.push(`/trips/${this.props.match.params.id}`)
      })
      .catch(console.error)
  }

  render () {
    return (
      <Fragment>
        <div className="row">
          <div className="col-sm-10 col-md-8 mx-auto mt-5">
            <h3 className="text-light">Activity</h3>
            <ActivityForm
              activity={this.state.activity}
              handleChange={this.handleChange}
              handleSubmit={this.handleSubmit}
            />
            <Button variant="secondary" href={`#trips/${this.props.match.params.id}`} size="sm">Back to trip</Button>
          </div>
        </div>
      </Fragment>
    )
  }
}

export default withRouter(CreateActivity)



